<template>
  <div class="navarrroot">
    <router-link
      class="prev-cont"
      v-if="data.previousLink.type == 'project' || data.previousLink.type == 'lecture'"
      :to="data.previousLink.link"
    >
      <div class="special-char">
        <img src="../images/prev.svg" alt="&lt;" />
      </div>
      <div class="message">
        {{ this.message() }}
        {{ data.previousLink.type }}{{ this.plural(false) }}
      </div>
    </router-link>
    <div class="placeholder" v-if="data.previousLink.type != 'project' && data.previousLink.type != 'lecture'"></div>
    <div class="short-title">{{ data.shortTitle }}</div>
    <div class="placeholder" v-if="data.nextLink.type != 'project' && data.nextLink.type != 'lecture'"></div>
    <router-link
      class="next-cont"
      v-if="data.nextLink.type == 'project' || data.nextLink.type == 'lecture'"
      :to="data.nextLink.link"
    >
      <div class="message">
        {{ this.message(true) }}
        {{ data.nextLink.type }}{{ this.plural(true) }}
      </div>
      <div class="special-char">
        <img src="../images/next.svg" alt="&gt;" />
      </div>
    </router-link>
  </div>
</template>

<script>
export default {
  name: "navarr",
  props: ["data"],
  methods: {
    message(isNext) {
      if (isNext) {
        if (this.data.nextLink.type != this.data.currentType) {
          return "Go to ";
        } else return "Next";
      } else {
        if (this.data.previousLink.type != this.data.currentType) {
          return "Go to ";
        } else return "Previous";
      }
    },
    plural(isNext) {
      if (isNext) {
        if (this.data.nextLink.type != this.data.currentType) {
          return "s";
        }
      } else {
        if (this.data.previousLink.type != this.data.currentType) {
          return "s";
        }
      }
      return;
    },
  },
};
</script>

<style scoped>
/** elements */
a:link {
  text-decoration: none;
  color: black;
}

a:active {
  text-decoration: none;
  color: black;
}

a:visited {
  text-decoration: none;
  color: black;
}
a:hover {
  text-decoration: none;
  color: black;
}
/** classes */
.navarrroot {
  padding: 20px 0px 20px 0px;
  width: 100%;
  display: flex;
  align-items: center;
}

.prev-cont {
  display: flex;
  justify-content: left;
  /* line-height: 70px; */
  width: 180px;
  /* margin: 0px auto 0px 0px 0px; */
}
.next-cont {
  display: flex;
  justify-content: right;
  /* line-height: 70px; */
  width: 180px;
  /* margin: 0px 0px 0px auto; */
}

.special-char {
  display: block;
  /* font-size: 50px; */
  width: 30px;
}
.message {
  line-height: 30px;
  display: block;
  color: #747474;
  font-size: 14px;
}

.short-title {
  transform: translateY(-3px);
  font-size: 22px;
  margin: auto;
}

.placeholder {
  display: flex;
  width: 180px;
}
/** IDs */

/** media Queries */
@media only screen and (max-width: 900px) {
  .message {
    display: none;
  }
  .prev-cont {
    width: 45px;
  }
  .next-cont {
    width: 45px;
  }
  .placeholder {
    width: 45px;
  }
}
</style>
